@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-family: 'Roboto Condensed', arial, sans-serif;
  font-size: 20px;
}
div.App{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: 0 auto;
}

header{
    flex-basis: 30vh;
}

main{
    display: flex;
    padding: 30px 0px;
    flex-grow: 1;
}

aside{
    min-width: 240px;
}

.page{
    flex-grow: 1;
}

footer{
    background-color: black;
    color: white;
    padding: 10px 0px;
    text-align: center;
    text-transform: uppercase;
}

@media(orientation: portrait) and (max-width: 768px) {

    header{
        flex-basis: 20vh;
    }
}

@media(orientation: portrait) and (max-width: 600px) {

    aside{
        min-width: 100px;
        width: 120px;
    }

    header{
        flex-basis: 18vh;
    }
}



@media(orientation: portrait) and (max-width: 415px) {

    aside{
        min-width: 100px;
        width: 120px;
    }

    header{
        flex-basis: 15vh;
    }
}


@media(orientation: landscape) and (max-width: 812px) {

    aside{
        min-width: 100px;
        width: 120px;
    }

    header{
        flex-basis: 20vh;
    }
}


header{
    position: relative;
    overflow: hidden;
}

header img{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-height: 100%;
    min-width: 100%;
}

@media(orientation: portrait) and (max-width: 415px) {
    header img{
        min-height: auto;
        min-width: auto;
    }
}
nav.main{
    padding: 0 30px 30px 0;
}

.main ul{
    list-style: none;
}

.main a{
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    color: black;
    font-weight: bold;
    padding: 10px;
    transition: 0.2s;
}

.main a:hover{
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}

.main a.active{
    border-left: 10px gray solid;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.main li:nth-child(1) a.active{
    border-left-color: red;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.main li:nth-child(2) a.active{
    border-left-color: green;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.main li:nth-child(3) a.active{
    border-left-color: blueviolet;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.main li:nth-child(4) a.active{
    border-left-color: blue;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}


@media(orientation: portrait) and (max-width: 415px) {

    nav ul{
        font-size: 13px;
    }

    .main a.active{
        border-left: 5px gray solid;
    }
}

@media(orientation: portrait) and (max-width: 600px) {

    nav ul{
        font-size: 13px;
    }

    .main a.active{
        border-left: 5px gray solid;
    }
}

@media(orientation: landscape) and (max-width: 812px) {

    nav ul{
        font-size: 15px;
    }

    .main a.active{
        border-left: 5px gray solid;
    }
}
