.contact textarea{
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6;
}

.contact button{
    display: block;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    /* margin: 0 20% 0 auto; */
    border: 2px solid black;
    background-color: #fff;
    padding: 10px 20px;
    font-size: 20px;
    text-transform: uppercase;
    cursor: pointer;
    transition: 0.2s;
}

.contact button:hover{
    background-color: black;
    color: white;
}

@media(orientation: portrait) and (max-width: 415px) {
    .contact textarea{
        font-size: 10px;
        width: 80%;
        min-height: 100px;
        margin: 30px 0;
        line-height: 1;
    }
    
    .contact button{
        border: 2px solid black;
        padding: 5px 10px;
        font-size: 10px;
    }

    .contact form h3{
        font-size: 12px;
    }
   
}

@media(orientation: landscape) and (max-width: 812px) {
    .contact textarea{
        font-size: 10px;
        width: 80%;
        min-height: 100px;
        line-height: 1;
    }
    
    .contact button{
        border: 2px solid black;
        padding: 5px 10px;
        font-size: 10px;
    }

    .contact form h3{
        font-size: 16px;
    }
   
}
